@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ingredients {
  display: grid;
  justify-content: center;
  align-items: center;
  margin: 2rem;
}

.ingredient-title {
  margin-bottom: 1rem;
  margin-top: 2rem;
  padding: 6px;
  background-color: #292929;
  color: #fafaff;
  border-radius: 5px;
  text-align: center;
}

.ingredient-list {
  display: grid;
  justify-content: flex-start;
  margin-left: 1rem;
  list-style-type: square;
}

.ingredient-list-step {
  padding: 5px;
}

.full-recipe-btn {
  width: 7rem;
  height: 2.2rem;
  padding: 5px;
  background-color: #0048ff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 1rem;
}

@media only screen and (max-width: 908px) {
  .ingredient-title {
    margin-top: 0;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.loader {
  color: #292929;
  font-size: 50px;
  text-indent: -9999em;
  overflow: hidden;
  border-radius: 50%;
  margin: 72px auto;
  position: fixed;
  top: 35%;
  left: 48%;
  width: 1em;
  height: 1em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
  animation: load6 1.7s infinite ease, round 1.7s infinite ease;
}

@media only screen and (max-width: 908px) {
  .loader {
    position: fixed;
    top: 35%;
    left: 43%;
  }
}

@-webkit-keyframes load6 {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em,
      -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em,
      -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em,
      -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em,
      -0.749em -0.34em 0 -0.477em;
  }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em,
      -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em,
      -0.82em -0.09em 0 -0.477em;
  }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}
@keyframes load6 {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em,
      -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em,
      -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em,
      -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em,
      -0.749em -0.34em 0 -0.477em;
  }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em,
      -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em,
      -0.82em -0.09em 0 -0.477em;
  }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}
@-webkit-keyframes round {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes round {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.selected-container {
  display: flex;
  justify-content: flex-start;
}

.selected-recipe-name {
  color: #292929;
  width: 17rem;
  margin: 5px;
  margin-left: 9px;
  text-align: center;
}

.meal-type {
  text-transform: capitalize;
  font-weight: bolder;
  margin-top: 10px;
  width: 17rem;
  text-align: center;
  color: #878484;
}

.active-recipe {
  margin: 2rem;
}

.activeRecipe-img {
  border: 1px solid #292929;
  border-radius: 1rem;
}

@media only screen and (max-width: 908px) {
  .selected-container {
    display: grid;
    justify-content: space-evenly;
    align-items: center;
    margin: 2rem;
  }

  .active-recipe {
    display: grid;
    justify-content: center;
    align-items: center;
  }
}

* {
  margin: 0;
  box-sizing: border-box;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Prompt", sans-serif;
  font-size: 2.5rem;
  font-weight: 400;
  margin-top: 1rem;
  text-transform: capitalize;
  letter-spacing: 3px;
  color: #0048ff;
}

.caption {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.search-form {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

input {
  margin-right: 5px;
  width: 10rem;
  height: 2.5rem;
  padding: 4px;
  border: 1px solid #dbdbdb;
  box-shadow: 0px 0px 10px 0px rgba(219, 219, 219, 0.5);
  border-radius: 5px;
}

.search-btn {
  background-color: #0048ff;
  border: none;
  color: #f9f9f9;
  border-radius: 5rem;
  height: 2.3rem;
  width: 2.3rem;
  cursor: pointer;
  transition-duration: 0.3s;
}

.recipe {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.container {
  display: grid;
  justify-content: center;
  text-align: start;
  margin: 2rem;
  margin-top: 2px;
  padding: 5px;
}

.recipe-name {
  color: #292929;
  font-size: 1.3rem;
  margin-left: 2rem;
  width: 20rem;
}

.recipe-img {
  margin: 1rem;
  border-radius: 2.5rem;
  width: 20rem;
  height: 20.5rem;
}

.recipe-type {
  text-transform: capitalize;
  margin-left: 2rem;
  color: #878484;
}

.calories {
  margin-left: 2rem;
  color: #878484;
}

.recipe-btn {
  width: 5.5rem;
  margin-left: 2rem;
  margin-top: 5px;
  border: none;
  background-color: #0048ff;
  color: white;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

